import './App.css';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  NavLink
} from "react-router-dom";

import Home from "./pages/Home";
import Stocks from "./pages/Stocks";
import Charts from "./pages/Charts";

const activeStyle = {
  fontWeight: "bold",
  color: "red"
};


export default function App() {
  return (
    <Router>
      <div>
          <ul>
            <li>
              <NavLink exact to="/"activeStyle={activeStyle}>
                Home
                </NavLink>
            </li>
            <li>
              <NavLink to="/Stocks"activeStyle={activeStyle}
              >Stocks
              </NavLink>
            </li>
            <li>
              <NavLink to="/Charts"activeStyle={activeStyle}
              >Charts
              </NavLink>
            </li>
          </ul>

        <Switch>
          <Route path="/">
            <Home/>
          </Route>
          <Route path="/Stocks">
            <Stocks />
          </Route>
          <Route path="/Charts">
            <Charts />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

